.hubspot-forms > div > div {
  display: flex !important;
  justify-content: center;
}
.hubspot-forms form {
  color: #a8aaad;
  font-size: 17px;
  display: table;
  width: 73%;
}
.hubspot-forms .hs-form-field {
  margin-top: 17px;
  font-family: "Roboto", sans-serif;
  font-size: 28px;
  font-weight: 300;
  font-style: normal;
  font-stretch: normal;
  line-height: 1;
  width: 100%;
  letter-spacing: -0.1px;
  color: #d8d8d8;
}
.hubspot-forms .hs-form-field label {
  font: 100%/120% Tahoma, Arial, Helvetica, Verdana, sans-serif;
  padding-right: 10px;
  position: relative;
  text-align: end;
  display: table-cell;
  color: #c1c5c9;
  opacity: 0.4;
  line-height: 4;
  font-size: 16px;
  font-weight: 300;
  outline: none;
  width: 25%;
}
.hubspot-forms .hs-form-field ul li {
  list-style: none;
}
.hubspot-forms .hs-form-field ul li input {
  width: auto;
  margin-right: 15px;
}
.hubspot-forms .hs-form-field ul li label {
  text-align: start;
  display: unset;
  color: #c1c5c9;
  opacity: 0.4;
  line-height: 4;
  font-size: 16px;
  outline: none;
  width: auto;
}
.hubspot-forms .hs-form-field .input {
  display: table-cell;
  width: 47rem;
}
.hubspot-forms .hs-form-field .hs-form-required {
  margin-left: 5px;
  position: absolute;
  color: #efb816;
  font-size: 16px;
}
.hubspot-forms .hs-form-field select {
  padding-bottom: 15px;
  color: #c1c5c97d;
}
.hubspot-forms .hs-form-field input,
textarea,
select {
  padding-left: 15px;
  width: 100%;
  background: transparent;
  border: none;
  border-bottom: solid 1px #c1c5c9;
  color: #c1c5c9;
  line-height: 4;
  font-size: 16px;
  outline: none;
}

input.hs-button {
  border: solid 2px #efb817;
  background-color: transparent;
  text-decoration: none;
  font-family: "Roboto", sans-serif;
  font-size: 13px;
  font-weight: 800;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: 1.5px;
  text-align: center;
  color: #ffffff;
  padding: 14px 45px;
  margin: 50px 0 25px 55px;
  cursor: pointer;
  outline: none;
}

@media (max-width: 768px) {
  .hubspot-forms form {
    width: 85%;
  }
